// Copy-pasted from marketing CSS
.nav {
    margin-top: 38px;
    padding-left: 15px;
    padding-right: 15px;
    display: table;
    z-index: 1;

    .nav-elements,
    .nav-logo {
        display: table-cell;
        vertical-align: middle;
    }
    .nav-logo a {
        display: block;
        svg .lettering {
            fill: #fff;
        }
    }
    @media (min-width: $screen-sm-min) {
        margin-top: 35px;

        .nav-elements {
            float: right;
        }
    }

    .nav-elements {
        #nav-toggle {
            cursor: pointer;
            .nav-toggle-burger,
            .nav-toggle-close {
                background-repeat: no-repeat;
                background-size: 100%;
                float: right;
            }
            .nav-toggle-burger {
                display: block;
                width: 27px;
                height: 23px;
                background-image: url(../vectors/homepage/menu-icon-39f9588453.svg);

                &:hover {
                    background-image: url(../vectors/homepage/menu-icon-hover-be9d6c3bfa.svg);
                }
            }
            .nav-toggle-close {
                display: none;
                width: 23px;
                height: 23px;
                background-image: url(../vectors/homepage/close-icon-2bab6f0b57.svg);

                &:hover {
                    background-image: url(../vectors/homepage/close-icon-hover-55a85a13a5.svg);
                }
            }
            img {
                height: 23px;
            }
        }

        ul {
            margin: 0;
            width: 100%;
            display: table;

            li {
                padding: 0;
                margin-bottom: 16px;
                display: block;
                text-align: center;
                opacity: 0;
                transition: opacity 1s;

                a {
                    font-size: 24px;
                    font-size: 2.4rem;
                    font-family: proximanovasoft-semibold, sans-serif;
                    font-style: normal;
                    color: #866b80;

                    &.btn {
                        font-size: 20px;
                        font-size: 2rem;
                        color: #fff !important;
                    }
                    &.active, &:active, &:focus, &:hover {
                        color: #5d5066;
                        text-decoration: none;
                    }

                    @media (min-width: $screen-sm-min) {
                        font-size: 16px;
                        font-size: 1.6rem;
                        color: rgba(255, 255, 255, .75);

                        &.btn {
                            font-size: 16px;
                            font-size: 1.6rem;
                        }
                        &.active, &:active, &:focus, &:hover {
                            color: #fff;
                        }
                    }
                }

                @media (min-width: $screen-sm-min) {
                    padding-left: 28px;
                    margin: 0;
                    display: table-cell;
                    vertical-align: middle;
                    text-align: left;
                    opacity: 1;
                }
            }

            @media (min-width: $screen-sm-min) {
                display: table-cell;
                vertical-align: middle;
                text-align: none;
            }
        }
    }
}

.nav-container {
    height: 0;
    transition: height .4s ease-in;

    // expanded (only should happen at small sizes)
    &.open {
        height: 550px;
        position: relative;
        background-color: #fff;
        box-shadow: 0 0 25px rgba(0, 0, 0, .35);
        z-index: 4;

        #nav-toggle {
            .nav-toggle-burger {
                display: none;
            }
            .nav-toggle-close {
                display: block;
            }
        }
         .nav-logo svg {
            .img {
                stroke: #ff4d63;
            }
            .img-beta,
            .lettering {
                fill: #ff4d63;
            }
        }

        .nav-elements .nav-list {
            height: 350px;
            width: 100%;
            position: absolute;
            top: 45px;
            left: 0;

            // this thing intercepts clicks on mobile, so we just hide it
            .dropdown-backdrop {
                @media (max-width: $screen-xs-max) {
                    display: none;
                }
            }

            ul {

                &:not(.dropdown-menu) {
                    display: block !important;
                }
                &.dropdown-menu {
                    position: relative;
                    width: 100%;
                    top: 0;
                    left: 0;
                    box-shadow: none;
                    border: none;
                    float: none;
                    border-top: 1px solid #ccc;
                    border-bottom: 1px solid #ccc;
                    margin-top: 10px;
                    margin-bottom: 5px;

                    li {
                        opacity: 1;
                        margin: 0;
                    }
                }

                li {
                    &:last-of-type {
                        margin-bottom: 0;
                    }

                    &.animate {
                        opacity: 1;
                    }

                    &.dropdown {
                    }
                }

                a.btn {
                    margin-top: 13px;
                    padding: 10px 23px;
                }
            }
        }

        &.small {
            height: 373px;
            .nav-elements .nav-list {
                height: 300px;
                ul {
                    top: 145px;
                }
            }
        }
    }
}