#sticky-navbar {
    background: transparent;

    @media (min-width: $screen-sm-min) {
        padding-bottom: 10px;
    }

    &,
    nav {
        transition: all 0.75s ease;
    }
    nav {
        .nav-logo a {
            min-width: 200px;

            span {
                line-height: 32px;
                display: inline-block;
                vertical-align: middle;
            }
        }

        @media (max-width: $screen-xs-max) {
            padding-bottom: 10px;
        }

        .nav-elements ul li {
            // size adjustments from pixelmatters base
            &, a:not(.btn) {
                line-height: 1.1em;
            }

            a {
                @media (min-width: $screen-sm-min) {
                    font-size: 12px;
                    font-size: 1.2rem;
                    &.btn {
                        padding-left: 10px;
                        padding-right: 10px;
                        min-width: 110px;
                    }
                }

                @media (min-width: $screen-md-min) {
                    font-size: 16px;
                    font-size: 1.6rem;
                    &.btn {
                        padding-left: 15px;
                        padding-right: 15px;
                    }
                }
            }

            // restore working dropdown menus
            &.dropdown {
                .caret {
                    transition: transform 0.5s;
                }

                &.open {
                    a.dropdown-toggle {
                        background: none;
                    }
                    ul.dropdown-menu {
                        display: block;

                        @media (max-width: $screen-xs-max) {
                            opacity: 1;
                        }

                        @media (min-width: $screen-sm-min) {
                            animation: fade-in-down-subtle 0.5s ease;
                            animation-fill-mode: both;
                        }

                        li {
                            display: block;
                        }
                    }
                    .caret {
                        transform: rotateZ(-180deg);
                    }
                }

                ul.dropdown-menu {
                    width: auto;
                    display: none;
                    opacity: 0;

                    li {
                        display: none;
                        padding: 5px 0px;

                        a {
                            color: $LOGIN_REGISTER_TEXT_GRAY;
                            padding: 5px 15px;
                        }
                    }

                    @media (min-width: $screen-sm-min) {
                        left: 12px;
                    }
                }
            }
        }
    }
    .nav-container.open nav {
        padding-bottom: 0px;
    }

    // when it's sticky
    &.header-shrink {
        background: #1A4797;

        nav {
            margin-top: 10px;
        }
    }
}