
// bundled FontSpring assets (see also: vendor/common/fonts + gulp/build.js::compile-fonts)
$FONT_FAMILY_REGULAR: "ProximaNovaSoft-Regular", sans-serif;
$FONT_FAMILY_SEMIBOLD: "ProximaNovaSoft-Semibold", sans-serif;
$FONT_FAMILY_MEDIUM: "ProximaNovaSoft-Medium", sans-serif;
$FONT_FAMILY_BOLD: "ProximaNovaSoft-Bold", sans-serif;
$FONT_FAMILY_ITALICS: "ProximaNova-RegIt", sans-serif;


$MOBILE_FONT_MULTIPLIER: 0.8;

// we should probably deprecate these guys in favor for component-driven styling
// possibly using EM+REM, but definitely accounting for media breakpoints
$FONT_SIZE_DEFAULT: 15px;
$FONT_SIZE_CONTENT: 20px;
$FONT_SIZE_CONTENT_SMALLER: 20px;
$FONT_SIZE_CONTENT_SCREEN_SMALL_MIN: 15px;  // Responsive

$FONT_SIZE_H1_DEFAULT: 35px;
$FONT_SIZE_H2_DEFAULT: 20px;
$FONT_SIZE_H3_DEFAULT: 18px;
$FONT_SIZE_H4_DEFAULT: 15px;



// NOTE: Should only be used in
@mixin primary-font {

    font-family: $FONT_FAMILY_REGULAR;
    font-size: $FONT_SIZE_DEFAULT;
    font-weight: normal;
    font-style: normal;
    color: $COLOR_V3_EGGPLANT;

    h1 {
        font-family: $FONT_FAMILY_REGULAR;
        font-weight: normal;
        font-style: normal;
    }
    h2, h3 {
        font-family: $FONT_FAMILY_SEMIBOLD;
    }

    h1 {
        $FONT_SIZE_H1_DEFAULT: 35px;
        font-size: $FONT_SIZE_H1_DEFAULT;
        margin: 10px 0;

        @media (max-width: $screen-xs-max) {
            font-size: $FONT_SIZE_H1_DEFAULT*$MOBILE_FONT_MULTIPLIER;
        }
    }

    h2 {
        $FONT_SIZE_H2_DEFAULT: 20px;
        font-size: $FONT_SIZE_H2_DEFAULT;
        margin: 8px 0;

        @media (max-width: $screen-xs-max) {
            font-size: $FONT_SIZE_H2_DEFAULT*$MOBILE_FONT_MULTIPLIER;
        }
    }

    h3 {
        $FONT_SIZE_H3_DEFAULT: 18px;
        font-size: $FONT_SIZE_H3_DEFAULT;
        margin: 6px 0;

        @media (max-width: $screen-xs-max) {
            font-size: $FONT_SIZE_H3_DEFAULT*$MOBILE_FONT_MULTIPLIER;
        }
    }

    h4 {
        $FONT_SIZE_H4_DEFAULT: 15px;
        font-family: $FONT_FAMILY_REGULAR;
        font-weight: normal;
        font-style: normal;
        font-size: $FONT_SIZE_H4_DEFAULT;
        margin: 4px 0;

        @media (max-width: $screen-xs-max) {
            font-size: $FONT_SIZE_H4_DEFAULT*$MOBILE_FONT_MULTIPLIER;
        }
    }

    h1, h2, h3, h4 {
        &.inline {
            display: inline;
        }
    }
}


// Insert a font awesome icon given its unicode value
// Useful for icons in :before and :after blocks
@mixin font-awesome-icon($unicode) {
    font-family: FontAwesome;
    font-weight: normal;
    font-style: normal;
    text-decoration: inherit;
    content: $unicode;
}
