body.mba.freemba.aboutmba {
    .hero {
    }

    .info-section {
        text-align: center;

        .infographic {
            img {
                max-width: 100%;
                height: auto;
                display: block;
                margin: 0px auto;
                @media (min-width: $screen-md-min) {
                    max-width: 800px;
                }
            }
        }

        ul {
            color: $COLOR_MARKETING_FREEMBA_GRAY;
            text-align: left;
            display: block;

            @media (min-width: $screen-md-min) {
                max-width: 500px;
                margin: 0px auto;
            }
        }
    }
}