body.mba.freemba.efficacy {
    .hero .hero-info {
        p {
            @media (min-width: $screen-sm-min) {
                margin-left: 70px;
                margin-right: 70px;
            }
        }
    }

    .info-section {
        text-align: center;
        &.study-section {
            text-align: left;

            .efficacy-chart {
                margin-left: auto;
                margin-right: auto;
                margin-bottom: 60px;

                @media (min-width: $screen-sm-min) {
                    max-width: 500px;
                }
            }
        }
    }
}