
body.mba.freemba {

    .course-icons {
        width: 100%;
        max-width: 1280px;
        height: auto;
        display: none;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        opacity: 0;
        transition: opacity 0.5s;

        @media (min-width: 768px) {
            display: block;
        }
    }

    .hero {
        position: relative;

        .hero-background {
            position: absolute;
            display: block;
            width: 100%;
            height: 100%;
            background-size: cover;
            background-position: center top;
            overflow: hidden;
            opacity: 0.85;
        }

        &.gradient-blue {
            background: linear-gradient(135deg, #194695 0, #4d7aff 100%);
        }

        &.gradient-blue-green {
            background: linear-gradient(135deg, #194695 0, #1DD0B4 100%);
        }

        &.gradient-blue-green-bright {
            background: linear-gradient(135deg, #4D7AFF 0, #1DD0B4 100%);
        }

        &.gradient-blue-green-faded {
            background: linear-gradient(135deg, #3F65D4 0, #18A893 100%);
        }

        &.gradient-flat-black {
            background: #000;

            h1, p, span {
                text-shadow: 0px 0px 10px rgba(0,0,0,0.3);
            }

            .hero-background {
                opacity: 0.55;
            }
        }

        &.gradient-darker {
            .hero-background {
                opacity: 0.55;
            }
        }

        &.gradient-darkest {
            .hero-background {
                opacity: 0.35;
            }
        }

        .course-icons {
            opacity: 0.5;
        }

        .hero-info {
            padding: 0 30px;
            max-width: 510px;
            position: absolute;
            top: 200px;
            left: 50%;
            transform: translateX(-50%);

            .hero-btns {
                opacity: 1;
                font-family: $FONT_FAMILY_REGULAR;

                .btn {
                    opacity: 1;
                    margin-bottom: 0;
                }

                // This stuff is to handle the input/button combo
                // at /hiring
                &.with-input {

                    .btn {
                        display: inline-block;
                        margin-bottom: 6px;
                    }

                    input {
                        border-radius: 4px;
                        height: 58px;
                        width: 100%;
                        max-width: 380px;
                        font-size: 21px;
                        padding-left: 12px;
                        color: black;
                        vertical-align: middle;
                        margin: 0px 6px 6px 0px;
                    }

                }





            }

            .hero-btns-subtitle {
                display: block;
                max-width: 220px;
                margin: 10px auto 0px;
                font-size: 1.1em;
                line-height: 1.2em;

                @media (min-width: 768px) {
                    max-width: 100%;
                }

                a {
                    color: #fff;
                    cursor: pointer;
                    .no-touchevents &:hover {
                        text-decoration: underline;
                    }
                }
            }

            h1 {
                font-family: $FONT_FAMILY_SEMIBOLD;
                font-style: normal;
                font-size: 40px;
                font-size: 4rem;
                line-height: 48px;
            }

            @media (max-width: 410px) {
                h1 {
                    font-size: 3rem;
                    font-size: 30px;
                    line-height: 38px;
                }
            }

            @media (min-width: 768px) {
                max-width: inherit;
                padding: 0 15px;
                top: 175px;
                left: inherit;
                transform: translateX(0);

                p {
                    margin-bottom: 30px;
                }

                h1 {
                    font-size: 50px;
                    font-size: 5rem;
                    line-height: 48px;
                    margin-bottom: 30px;
                }
            }

            @media (min-width: 992px) {
                p {
                    margin-bottom: 50px;
                }
            }
        }
    }
}