body.mba.freemba {
    .info-section.team {
        text-align: center;

        .team-member {
            .team-member-name {
                font-family: $FONT_FAMILY_SEMIBOLD;
                font-style: normal;
                font-size: 24px;
                font-size: 2.4rem;
                color: #666;
                margin-bottom: 4px;
            }

            .team-member-description {
                color: $COLOR_MARKETING_FREEMBA_GRAY;
            }

            &.leadership {
                .team-member-position {
                    margin-bottom: 0px;
                }
            }
        }

        h2 {
            color: $COLOR_V3_CORAL;
            &.department-title {
                margin-top: 20px;
                margin-bottom: 64px;
            }
        }

        hr {
            margin: 10px auto 90px;
            color: $COLOR_MARKETING_FREEMBA_GRAY;
            height: 1px;
            max-width: 300px;

            @media (min-width: $screen-sm-min) {
                margin-top: 80px;
            }
        }

        p {
            font-size: 16px;
            font-size: 1.6rem;
            line-height: 1.63;

            &.team-member-description {
                margin-bottom: 0;
                margin-top: 21px
            }
        }

        @media (min-width: $screen-sm-min) {
            h2 {
                font-size: 28px;
                font-size: 2.8rem;

                &.department-title {
                    margin-top: 128px;
                    margin-bottom: 73px;
                }
            }
            p {
                font-size: 19px;
                font-size: 1.9rem;
                line-height: 29px;

                &.team-member-description {

                }
            }
        }
        @media (min-width: $screen-md-min) {
            h2 {
                font-size: 35px;
                font-size: 3.5rem;
            }
        }
    }
}