.talent-faces {
    position: relative;
    display: block;
    margin-top: 25px;
    margin-bottom: 25px;

    @media (max-width: $screen-xs-max) {
        height: 500px;
        overflow: hidden;

        &:after {
            content: '';
            position: absolute;
            display: block;
            width: 100%;
            height: 100px;
            left: 0px;
            bottom: 0px;
            @include gradient-vertical(rgba($COLOR_V3_WHITE,0), rgba($COLOR_V3_WHITE,1));
        }
    }

    .face {
        display: inline-block;
        border-radius: 50%;
        border: solid 4px #fff;
        box-shadow: 0 2px 2px 0 rgba(0,0,0,0.25);
        transition: all 0.5s;
        margin: 5px;

        @for $i from 1 through 12 {
            &:nth-of-type(#{$i}) {
                transition-delay: #{$i/25}s;
            }
        }
    }
}