.courses-visible .info-section-courses .btn-reveal,
.courses-visible .info-section-courses:after {
    display: none
}
// .info-section-courses {
//     position: relative;
//     background-color: #fafafa
// }
.info-section-courses-list:after {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-image: linear-gradient(to top, #fff 0, rgba(255, 255, 255, 0) 50%, rgba(255, 255, 255, 0) 100%);
    content: ''
}
.info-section-courses .btn-reveal {
    position: absolute;
    bottom: 50px;
    left: 50%;
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    transform: translateX(-50%);
    z-index: 1
}
