// }

.courses-visible .info-section-courses .info-section-courses-list {
    height: auto;
    overflow: visible
}


.info-section-courses-list {
    height: 635px;
    padding-bottom: 50px;
    float: left;
    overflow: hidden;
    position: relative;

    h3 {
        margin-bottom: 40px;
        font-family: proximanovasoft-semibold, sans-serif;
        font-style: normal;
        font-size: 24px;
        font-size: 2.4rem;
        text-align: center;
        color: #525561;
    }

    .info-section-courses-item {
        display: table;
        padding: 16px 20px;
        width: 100%;
        height: 100px;
        min-height: 100px;
        margin-bottom: 40px;
        background-color: #fff;
        border: 1px solid #eee;
        border-radius: 4px;
        background-clip: padding-box;
        text-align: center;

        img {
            margin-right: 0px;
            margin-bottom: 15px;
        }

        h4 {
            margin: 0;
            padding: 0;
            font-family: proximanovasoft-semibold, sans-serif;
            font-style: normal;
            font-size: 17px;
            font-size: 1.7rem;
            line-height: 22px;
            color: #aaa;

            width: 100%;
            max-width: 100%;
        }

        @media (min-width: $screen-sm-min) {
            text-align: left;

            h4 {
                width: calc( 100% - 55px ); /* 40px icon + 15px margin */
                max-width: calc( 100% - 55px );
                display: inline-block;
                vertical-align: middle;
            }
            img {
                margin-right: 15px;
                margin-bottom: 0;
                display: inline-block;
                vertical-align: middle;
            }
        }

        .info-section-courses-item-inner {
            display: table-cell;
            vertical-align: middle;
        }
    }

    @media (min-width: 768px) {
        height: 495px;
    }
}
