body.mba.freemba.hiring {

    #sticky-navbar {
        &.header-shrink {
            background: #333;
        }
    }

    .hero {
        .hero-background {
            background-image: url(../images/hiring/hiring_bg-444026bf4e.jpg);
        }

        .hero-info {
            p {
                @media (min-width: $screen-sm-min) {
                    margin-left: 70px;
                    margin-right: 70px;
                }
            }
        }
    }
    .info-section {
        &.only-the-best {
            @media (min-width: $screen-sm-min) {
                padding-top: 80px;
                padding-bottom: 80px;
            }
        }

        &.strengthen-your-team {
            .vertical-align {
                margin-top: 0px;

                @media (min-width: $screen-md-min) {
                    margin-top: 20px;
                }
                @media (min-width: $screen-lg-min) {
                    margin-top: 55px;
                }
            }

            .info-image-left {
                img.img-responsive {
                    @media (max-height: $screen-xs-max) {
                        max-height: 250px;
                        margin: 0 auto;
                    }
                }
            }

            @media (min-width: $screen-sm-min) {
                padding-top: 100px;
                padding-bottom: 100px;
            }
        }

        &.home-features-section {
            @media (min-width: $screen-sm-min) {
                padding-bottom: 0px;
            }
        }

        &.the-perfect-hire {
            @media (min-width: $screen-sm-min) {
                padding-top: 80px;
                padding-bottom: 80px;
            }
        }
    }
}