
body.mba.freemba .info-section {
    .section-btns {
        display: block;
        text-align: center;
        a {
            display: inline-block;
        }
    }

    .graphic-column {
        text-align: center;
        margin-bottom: 0px;

        @media (min-width: $screen-sm-min) {
            margin-bottom: 60px;
        }
    }

    .full-width-svg {
        max-width: 100%;
        height: auto;
        margin-top: 20px;
        margin-bottom: 40px;
        padding: 20px;

        @media (min-width: $screen-sm-min) {
            margin-top: 60px;
            margin-bottom: 80px;
            padding: 0px;
        }
    }

    // paragraph styling overrides
    p {
        color: $COLOR_MARKETING_FREEMBA_GRAY;

        &.lead {
            max-width: 510px;
            margin: 0 auto 128px;
        }

        @media (min-width: 768px) {
            //margin-bottom: 100px;

            &.lead {
                max-width: inherit;
                margin-bottom: 153px;
            }
            &.big-gray {
                font-size: 24px;
                line-height: 28px;
            }
        }
    }

    &.scroll-bordered {
        position: relative;
        border-bottom: 1px solid #eee;
        padding-bottom: 60px;
        padding-top: 93px;
        overflow: visible;

        &:last-of-type {
            border-bottom: none;
        }

        p {
            margin-bottom: 2em;
        }

        span.final-line {
            color: #b5a99e;
            display: block;
            text-align: center;
            padding: 30px;
            margin: 10px auto;
            background-color: #F3EDE4;
            width: 100%;
            font-size: 140%;
            line-height: 1.1em;
            border: 10px solid white;
            box-shadow: 0px 0px 10px;

            @media (min-width: $screen-sm-min) {
                width: 330px;
            }
        }

        h2 {
            text-align: center;
            margin: 0 0 20px;
            font-size: 30px;
            font-size: 3rem;
            line-height: 35px;
            color: #615a52;

            @media (min-width: 768px) {
                margin-bottom: 40px;
                font-size: 35px;
                font-size: 3.5rem;
                line-height: 40px;
            }
        }

        .scroll-down-circle {
            display: block;
            position: absolute;
            width: 50px;
            height: 50px;
            left: calc( 50% - 25px );
            bottom: -25px;
            border: 1px solid #eee;
            background-color: #fff;
            border-radius: 25px;
            z-index: 1000;
            color: #eee;
            cursor: pointer;

            i {
                display: block;
                position: absolute;
                font-size: 20px;
                top: 13px;
                left: calc( 50% - 10px );
                text-align: center;
            }

            &:hover {
                background-color: #eee;
                i {
                    color: #fff;
                }
            }
        }
    }


    &, &.scroll-bordered {
        .info-text-left, .info-text-right {
            @media (min-width: $screen-sm-min) {
                &, p, h2 {
                    text-align: left;
                }
            }
        }
    }

    .infographic-cutoff {
        margin: 50px auto;
        position: relative;
        text-align: center;
        overflow: hidden;
        padding: 0px 50px;

        &:hover {
            cursor: pointer;
        }

        @media (min-width: $screen-sm-min) {
            width: 600px;
            padding: 0px 75px;
            max-height: 300px;

            img {
                position: relative;
                transform: translateY(20px);
                transition: transform 0.25s ease;

                &:hover {
                    transform: translateY(0px);
                }
            }

            &:after {
                content: '';
                display: block;
                position: absolute;
                bottom: -15px;
                left: 0;
                width: 100%;;
                height: 30px;

                background-image: radial-gradient(ellipse, rgba($COLOR_V3_BEIGE_DARK, 1) 0%, rgba($COLOR_V3_BEIGE_DARK, 0) 70%);
            }
        }

        @media (min-width: $screen-md-min) {
            width: 700px;
        }
    }
}