body.mba.freemba.aboutmba {
    .hero {
    }

    .info-section {
        h2 {
            text-align: center;
        }

        p {
            text-align: center;
        }

        &.apply-for-the-degree {
            p.lead {
                text-align: left;
                &.center {
                    text-align: center;
                }
            }

            a:hover {
                cursor: pointer;
            }
        }

        .course-catalog {
            color: #aaa;
            font-size: 16px;
            margin-top: 20px;

            .btn {
                font-size: 16px;
                margin-top: 20px;
                display: block;
                padding: 20px;

                @media (min-width: $screen-sm-min) {
                    padding: 9px 18px 7px;
                    display: inline-block;
                    margin-top: 0;
                    margin-left: 10px;
                }
            }
        }
    }
}