body.mba.freemba .info-section.home-call-to-action-section {
    p {
        color: #fff;
        text-align: center !important;

        &.deadline-message {
            font-size: 1em;
            margin-top: 10px;
            margin-bottom: 0px;
        }
    }
}

/*
 * OVERRIDES to allow centering course columns
 */

.row-centered .col-centered {
    vertical-align: top;
}

.extended-course-column {
    text-align: center;
}

/*
 * OVERRIDES due to our font being different than theirs (I think)
 */
body.mba .info-section.info-section-left .info-section-text .btn {
    padding: 12px 34px 11px;
}

/*
 * OVERRIDES due to conflicting names of images
 */
.btn.btn-twitter.btn-bordered {
    background-image: url(../vectors/mba/homepage/twitter-icon-blue-ff318ae816.svg);
}

/*
 * Overides due to styling decisions
 */
body.mba strong {
    font-weight: normal;
}

/*
 * Modal for see all courses
 */
.modal .open {

}
.modal .coming-soon {
    opacity: 0.7;
}

/*
 * Inline Demo CSS Styling
 */
.info-section-demo {
    border: none !important;
}
.info-section-demo .container {
    padding: 0;
    width: 100% !important;
    margin: 0;
}
.info-section-demo .container iframe {
    background: #EEEBE8;
    border: none !important;
    border-bottom: 1px solid #eee !important;
}
/*
 * Underlined topic, animated
 */
body.mba.work .underlined-topic {
    border-bottom: 2px solid white;
    min-width: 120px;
    height: 38px;
    text-align: center;
    display: inline-block;
    vertical-align: top;
}
@media (min-width: 411px) {
    body.mba.work .underlined-topic {
        text-align: left;
        min-width: 160px;
        height: 48px;
    }
}
@media (min-width: 768px) {
    body.mba.work .underlined-topic {
        min-width: 180px;
        height: 54px;
    }
}

/*
 * Rocket graphic styling
 */
#rocket {
    overflow: visible;
}
#rocket #rocket-group {
    display: none;
    overflow: visible;
}

/*
 * Demo link at the bottom of the page
 */
.demo-footer-link-container p {
    margin-bottom: 0px !important;
}
.demo-footer-link-container a {
    color: #ffffff;
    border-bottom: 1px solid #ffffff;
    cursor: pointer;
}

/*
 * Generic link coloring
 */

a.coral {
    color: $COLOR_V3_CORAL;

    &:hover {
        color: $COLOR_V3_CORAL_DARK;
    }
}