.message-header {
    $messageColor: $COLOR_V3_BLUE;

    position: relative;
    background-color: $messageColor;
    color: $COLOR_V3_WHITE;
    height: auto;
    font-size: 15px;
    line-height: 1.2em;
    padding: 5px;
    margin-bottom: -15px;
    display: block;
    text-align: center;

    transform: translateY(-100%);
    animation: fade-in-down-subtle 1s;
    animation-fill-mode: forwards;

    transition: margin-bottom 0.75s ease;

    .header-shrink & {
        margin-bottom: 0px;
    }

    .stop-scroll & {
        display: none;
    }

    &:after {
        content: '';
        position: absolute;
        width: 100%;
        height: 40px;
        bottom: 100%;
        left: 0;
        background-color: $messageColor;
    }
}