body.freemba {

    a {
        color: $COLOR_V3_CORAL;

        .no-touchevents &:hover,
        .no-touchevents &:active,
        .touchevents &:active {
            color: $COLOR_V3_CORAL_DARK;
        }
    }

    .btn.btn-white, .btn.btn-red {
        &:hover {
            color: $COLOR_V3_WHITE;
        }
    }

    .btn.btn-turquoise {
        color: #fff;
        background: $COLOR_MARKETING_TURQUOISE;
        border: 1px solid $COLOR_MARKETING_TURQUOISE;
        box-shadow: 0 1.5px 0 rgba(0, 0, 0, .15);

        &:hover {
            background: $COLOR_MARKETING_TURQUOISE_HOVER;
            border: 1px solid $COLOR_MARKETING_TURQUOISE_HOVER;
        }
    }
    .btn.btn-bordered.btn-bordered-turquoise {
        border: 1px solid $COLOR_MARKETING_TURQUOISE;
        color: $COLOR_MARKETING_TURQUOISE;

        &:hover {
            background: $COLOR_MARKETING_TURQUOISE;
            color: #fff;
        }
    }

    .btn.btn-bordered.btn-bordered-gray {
        border: 1px solid $COLOR_MARKETING_FREEMBA_GRAY;
        color: $COLOR_MARKETING_FREEMBA_GRAY;
        font-family: $FONT_FAMILY_REGULAR;

        &:hover {
            background: $COLOR_MARKETING_FREEMBA_GRAY;
            color: #fff;
        }
    }
}