body.mba.freemba.index {
    // top hero section
    .hero .hero-info {
        @media (max-width: 410px) {
            top: 145px;
            h1 {
                font-size: 28px;
                line-height: 36px;
            }
        }

        @media (min-width: 411px) {
            top: 165px;
            max-width: 590px;
            h1 {
                font-size: 34px;
                line-height: 40px;
            }
        }
        @media (min-width: $screen-sm-min) {
            top: 165px;
            max-width: inherit;
            h1 {
                font-size: 44px;
                line-height: 46px;
            }
        }
        @media (min-width: $screen-md-min) {
            top: 130px;
        }

        p {
            @media (min-width: $screen-md-min) {
                margin-bottom: 30px;
            }
        }

        a.underlined {
            cursor: pointer;
            white-space: nowrap;

            &:hover {
                color: #eee;
                text-decoration: underline;
            }
        }

        .hero-btns {
            opacity: 0;
            font-family: proximanovasoft-regular, sans-serif;

            .btn {
                opacity: 1;
                margin-bottom: 0;
            }

            .hero-btns-subtitle {
                padding-top: 3px;

                a {
                    color: #fff;
                    cursor: pointer;
                    .no-touchevents &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }

        .hero-btns-subtitle {
            display: block;
            margin-top: 5px;
        }
    }

    #lightbulb-hero {
        pointer-events: none;
        position: absolute;
        bottom: -30px;
        left: 50%;
        transform: translateX(-50%);

        @media (min-width: $screen-md-min) {
            bottom: 3px;
        }

        @media (min-width: $screen-lg-min) {
            bottom: 5px;
        }

        #lightbulb-group {
            display: none;
        }

        #lightbulb-head-glow {
            filter: url('/#glowBulbFilter');
        }

        // the filter doesn't always render in Firefox, so disable it for Firefox only
        @-moz-document url-prefix() {
            #lightbulb-head {
                filter: none !important;
            }
        }

        // @media (min-width: 768px) {
        //     bottom: 2px;
        // }
        // @media (min-width: 992px) {
        //     bottom: 3px;
        // }
        // @media (min-width: 1200px) {
        //     bottom: 4px;
        // }
        // @media (min-width: 1440px) {
        //     bottom: 5px;
        // }
        // @media (min-width: 1740px) {
        //     bottom: 6px;
        // }
    }

    // info sections
    .info-section {

        &.smartly-mba-section {
            .vertical-align {
                margin-top: 0px;

                @media (min-width: $screen-lg-min) {
                    margin-top: 35px;
                }
            }
        }

        &.smartly-users-section {
            .vertical-align {
                margin-top: 0px;

                @media (min-width: $screen-md-min) {
                    margin-top: 30px;
                }

                @media (min-width: $screen-lg-min) {
                    margin-top: 65px;
                }
            }
        }

        &.smartly-at-work-section {
            .cityscape-box {
                margin-bottom: -60px;
            }
        }

        &.home-features-section {
            background-color: #fafafa;

            .home-feature {
                margin-bottom: 0px;
            }
        }

        &.testimonials-section {
            background-color: #f5f5f5;

            .grid-sizer, .grid-item {
                width: 100%;

                @media (min-width: $screen-sm-min) {
                    width: 33%;
                }

                &.special-grid-item {
                    .inner {
                        padding-top: 80px;
                    }

                    @media (min-width: $screen-sm-min) {
                        width: 100%;

                        .inner {
                            padding-top: 20px;
                            padding-left: 140px;
                        }
                    }

                    @media (min-width: $screen-md-min) {
                        width: 33%;

                        .inner {
                            padding-top: 130px;
                            padding-left: 20px;
                            height: 370px;
                        }
                    }

                    @media (min-width: $screen-lg-min) {
                        .inner {
                            height: 350px;
                        }
                    }
                }
            }

            .grid-item {
                padding: 10px;
                float: left;

                .inner {
                    position: relative;
                    padding: 20px;
                    background-color: white;
                    border: 1px solid #eee;
                    text-align: left;

                    .attribution-image-box {
                        position: absolute;
                        top: 20px;
                        left: 50%;
                        transform: translateX(-50%);
                        text-align: center;
                        width: 100px;

                        img {
                            max-width: 100%;
                            height: auto;
                            display: inline-block;
                        }

                        &.wp {
                            margin-top: 10px;
                            width: 150px;
                        }

                        @media (min-width: $screen-sm-min) {
                            left: 20px;
                            top: 50%;
                            transform: translateY(-50%);

                            &.wp {
                                margin-top: inherit;
                                width: 100px;
                            }
                        }

                        @media (min-width: $screen-md-min) {
                            top: 20px;
                            left: 50%;
                            transform: translateX(-50%);
                            width: 200px;

                            // adjustments to line up logos
                            &.hbr {
                                width: 150px;
                                margin-top: 10px;
                            }

                            &.cci {
                                margin-top: -5px;
                            }

                            &.wp {
                                margin-top: 25px;
                                width: 200px;
                            }
                        }
                    }

                    .quote {
                        padding-left: 30px;
                        position: relative;
                        line-height: 1.2em;
                        margin-bottom: 10px;
                        color: #666;

                        &:before {
                            content: "\201C";
                            position: absolute;
                            display: block;
                            left: 0px;
                            top: -4px;
                            font-size: 56px;
                            line-height: 1em;
                            color: $COLOR_MARKETING_FREEMBA_GRAY;
                            opacity: 0.5;
                        }
                    }

                    .attribution {
                        color: $COLOR_MARKETING_FREEMBA_GRAY;
                        padding-left: 30px;

                        a {
                            color: inherit;
                            &:hover {
                                text-decoration: underline;
                            }
                        }
                    }
                }
            }

            .star-rating-row {
                margin-top: 40px;
                margin-bottom: 0px;

                .star-image-box {
                    margin-bottom: 20px;

                    img {
                        max-width: 100%;
                        height: auto;

                        @media (min-width: $screen-sm-min) {
                            max-width: 440px;
                        }
                    }
                }
            }
        }

        &.network-section {
            text-align: center;
            p {
                img {
                    @media (min-width: $screen-sm-min) {
                        max-width: 600px;
                        margin: 0 auto;
                    }
                }
            }
        }

        &.awards-section {
            text-align: center;

            img.img-responsive {
                display: inline-block;
                transform: scale(0.5);

                @media (min-width: $screen-sm-min) {
                    margin-bottom: 0px;
                    transform: none;

                    padding: 15px;

                    &.dubai {
                        margin-top: 20px;
                    }
                }
            }
        }
    }
}