body.mba.freemba.recruit {
    .hero {
        height: 560px;

        .hero-info {
            p {
                @media (min-width: $screen-sm-min) {
                    margin-left: 70px;
                    margin-right: 70px;
                }
            }
        }
    }
}

body.mba.freemba.training {
    .hero {
        .hero-background {
            background-image: url(../images/training/training_bg-3671c352da.jpg);
        }
    }
}

body.mba.freemba.training,
body.mba.freemba.recruit {
    #sticky-navbar {
        &.header-shrink {
            background: #333;
        }
    }

    .hero {
        .hero-info {
            top: 150px;
        }
    }

    .info-section {
        text-align: center;
    }

    .typeform-section {
        position: relative;
        top: -235px;
        overflow: visible;
        padding-top: 0px;
        padding-bottom: 120px;
        margin-bottom: -235px;

        .recruiting-typeform-container {
            box-shadow: 0px 5px 15px $COLOR_MARKETING_FREEMBA_GRAY;
            background-color: $COLOR_V3_WHITE;
            border: 0px solid transparent;
            border-radius: 3px;
            overflow: hidden;
        }
    }
}

body.mba.freemba.training, body.mba.freemba.universities {
    .hero {
        .hero-btns {
            font-family: proximanovasoft-regular, sans-serif;

            .btn {
                margin-bottom: 0;
            }

            .hero-btns-subtitle {
                padding-top: 3px;

                a {
                    color: #fff;
                    cursor: pointer;
                    .no-touchevents &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }

        .hero-btns-subtitle {
            display: block;
            margin-top: 5px;
        }

        .hero-info {
            p {
                @media (min-width: $screen-sm-min) {
                    margin-left: 70px;
                    margin-right: 70px;
                }
            }
        }
    }

    .info-section {
        text-align: center;

        .training-box {
            border: 0px solid transparent;
            border-radius: 10px;
            background: $COLOR_V3_CORAL;
            padding: 20px 30px;
            margin-bottom: 15px;

            @media (min-width: $screen-sm-min) {
                margin-bottom: 30px;
                min-height: 160px;
            }

            h2 {
                font-size: 26px;
                font-family: $FONT_FAMILY_SEMIBOLD;
                line-height: 1em;
                margin-bottom: 10px;
                color: $COLOR_V3_WHITE;

                @media (min-width: $screen-sm-min) {
                    margin-bottom: 20px;
                }
            }
            p {
                color: $COLOR_V3_WHITE;
                line-height: 1em;
                margin: 0px;
            }
        }

        .matriculating-feature img {
            height: 185px;
            margin-top: 0px;
        }

        .flip-feature img {
            margin-top: -15px;
            height: 200px;
        }


        &.organizations-demo-courses {
            .separator {
                content: '';
                display: block;
                position: absolute;
                top: 0px;
                left: 50%;
                border-right: 1px solid #eee;
                height: 400px;
                width: 1px;
            }

            @media (min-width: $screen-sm-min) {
                .demo-col, .browse-col {
                    padding-left: 40px;
                    padding-right: 40px;

                    &:after {
                        display: none;
                    }
                }
            }
        }
    }
}