.coming-soon-box {
    width: 100%;
    padding: 20px;
    margin: 0 auto;
    color: $COLOR_MARKETING_FREEMBA_GRAY;
    border: 1px solid $COLOR_MARKETING_FREEMBA_GRAY;
    border-radius: 50px;
    font-size: 18px;
    text-align: center;

    @media (min-width: $screen-sm-min) {
        width: 50%;
    }
}