.app-store-row {
    margin-top: 50px;
    margin-bottom: 75px;

    .app-store-button {
        padding: 10px 40px;
        border: 1px solid $COLOR_MARKETING_FREEMBA_GRAY;
        border-radius: 10px;
        display: block;
        margin-bottom: 20px;
        text-align: center;

        background-color: $COLOR_MARKETING_FREEMBA_GRAY;
        svg > path {
            fill: $COLOR_V3_WHITE;
        }
        
        &.link {
            &:hover {
                color: $COLOR_V3_WHITE;
                background-color: darken($COLOR_MARKETING_FREEMBA_GRAY, 10%);
                svg > path {
                    fill: $COLOR_V3_WHITE;
                }
            }
        }

        &.coming-soon {
            opacity: 0.5;
        }

        svg {
            height: 50px;
            padding-top: 5px;
        }
    }
}

body.mba.freemba .info-section.scroll-bordered.app-store-section {
    p.lead.big-gray {
        margin-bottom: 0px;
        text-align: center;
    }
}